.custom-select {
  display: inline-block;
  position: relative;
  cursor: pointer;
  color: #b8b8b8;
  background-color: #ffffff;
  font-family: Panton-Regular, Arial, sans-serif;
  width: 100%;
}

.custom-select__field {
  padding: 22px 10px 19px;
}

.custom-select__dropdown {
  padding: 20px;
  background-color: #ffffff;
  line-height: 30px;
  border-radius: 30px;
  transition: all 0.2s;
  position: absolute;
  bottom: 0;
  transform: translateY(100%);
  visibility: hidden;
  opacity: 0;
  width: 100%;
}

.custom-select.is-open {
  border-radius: 30px 30px 0 0;
}

.custom-select.is-open .custom-select__dropdown {
  border-top: 1px solid #dce2e3;
  border-radius: 0 0 30px 30px;
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

.custom-select.is-open .custom-select__field .icon {
  transform: translateY(-50%) rotate(180deg);
}

.custom-select.is-open .custom-select__field .icon-label {
  transform: translateY(-50%) rotate(0);
}

.custom-select__list li:hover {
  color: #14c1e5;
}

.custom-select__field {
  margin-right: 30px;
  margin-left: 29px;
  padding-left: 24px;
  position: relative;
}

.custom-select__field .icon {
  transition: all 0.3s;
  height: 12px;
  width: 18px;
  stroke: #b8b8b8;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

.custom-select__field .icon-label {
  height: 14px;
  width: 17px;
  fill: #b8b8b8;
  left: 0;
}

