.doctors-list {
	background-color: #14c1e5;
	background-size: cover;
	background-position: center center;
	text-align: center;
	padding-top: 61px;
	padding-bottom: 53px;
	&__title {
		margin-bottom: 34px;
		font-size: 35px; }
	&__list {
		margin: 0 -70px; } }

.specialists {
	padding-left: 181px;
	@include clr;
	&__item {
		float: left;
		width: 50%;
		text-align: left;
		&:not(:last-child) {
			margin-bottom: 12px; }
		a {
			position: relative;
			padding-left: 33px;
			text-decoration: none;
			color: #fff;
			@include OSReg;
			font-size: 17px;
			span {
				position: absolute;
				left: 0;
				top: 0;
				width: 22px;
				height: 22px;
				background-color: #d0f3fa;
				border-radius: 50%;
				.icon {
					position: absolute;
					top: 50%;
					left: 50%;
					margin-left: -2px;
					margin-top: -4px;
					font-size: 8px;
					fill: #14c1e5; } } } } }

