@mixin m {
	@media only screen and (max-width: "991px") {
		@content; } }

body {
	@include OSReg;
	@include smooth-font;
	font-size: 16px;
	color: $black;
	&.is-hidden {
		overflow: hidden; } }
a {
	text-decoration: none;
	color: $black; }

h1 {
	font-size: 50px;
	@include PSemi;
	@include md {
		font-size: 25px; } }

h2 {
	font-size: 35px;
	@include PSemi;
	@include md {
		font-size: 25px; } }

h3, .h3 {
	font-size: 30px;
	@include PSemi;
	@include md {
		font-size: 20px; } }

h4, .h4 {
	font-size: 20px;
	@include PSemi;
	@include md {
		font-size: 14px; } }

.container {
	max-width: 1260px;
	margin: 0 auto;
	padding: 0 40px;
	@include clr; }
.col {
	float: left;
	&_lg-left {
		width: percentage(900/1180);
		padding-right: 20px; }
	&_lg-right {
		width: percentage(900/1180);
		padding-left: 20px; }
	&_sm {
		width: percentage(280/1180); } }

.text {
	line-height: 24px; }
.text-cyan {
	color: $cyan; }
.text-bold {
	@include OSBold; }

.list {
	line-height: 24px;
	margin: 20px 0;
	&__item {
		position: relative;
		padding-left: 15px;
		&:before {
			font-size: 23px;
			display: block;
			position: absolute;
			content: '';
			color: $cyan;
			top: 0;
			left: 0; } } }
.map-link {
	display: block;
	border-radius: 5px;
	overflow: hidden;
	position: relative;
	p {
		@include PSemi;
		color: $white;
		text-align: center;
		width: 100%;
		background-color: $cyan;
		padding: 9px 5px 8px;
		position: absolute;
		left: 0;
		bottom: 0; }
	&--search {
		height: 264px;
		width: 100%;
		max-width: 280px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } } }
.out {
	&--bg-gray {
		background-color: #fafafa; } }
.article {
	color: $black;
	@include OSReg;
	a {
		color: $cyan; }
	&--sm {
		font-size: 14px; }
	&--mb-95 {
		margin-bottom: 95px; } }

.centered {
	text-align: center; }

.bg-white {
	background-color: $white; }
