.doctor {
	display: flex;
	&__left {
		margin-right: 30px;
		.btn {
			width: 175px; } }
	&__right {
		flex-grow: 1; }
	&__avatar {
		margin-bottom: 20px;
		height: 220px;
		width: 175px;
		overflow: hidden;
		border-radius: 5px;
		img {
			height: 100%;
			width: 100%;
			object-fit: cover; } }
	&__list {
		font-size: 14px;
		line-height: 24px;
		span {
			@include OSBold; } }
	&__experience {
		font-size: 15px;
		@include PBold;
		color: #cdd0d1;
		margin-bottom: 14px; }
	&__lists {
		h6 {
			font-size: 14px;
			@include OSBold; }
		.list {
			margin: 8px 0 16px; } }

	.topline {
		display: flex;
		justify-content: space-between; } }
